        var self = this;

        if(url.indexOf('http://')!=0){
          url = 'http://119.23.229.133'+url
        }


        Vue.http.get(url).then(function(r){

          self.response = self.formatHtml(r.data);

        }).catch((res) => {
          self.response = res.status+" "+res.statusText;

        });

