<template>
  <div class="applicationIn main_content clearfix">
    <div class="tab_type_box">
      <div class="tab_type_head">
        <a class="active"></a>
      </div>
      <div class="applicationIn_content">
        <section>
          <span class="lable"></span>
          <span v-for="item in types" :key=item class="types" :class="{'active': typesValue=== item.value}" @click="types_tab(item.value)">{{item.name}}</span>
        </section>
        <section>
          <span class="lable"></span>
          <input type="text" value="" placeholder="">
        </section>
        <section>
          <span class="lable"></span>
          <select>
            <option value="1"></option>
          </select>
          <select>
            <option value="1"></option>
          </select>
          <select>
            <option value="1"></option>
          </select>
        </section>
        <section>
          <span class="lable"></span>
          <input type="text" value="" placeholder="">
        </section>
        <section>
          <span class="lable"></span>
          <input type="text" value="" placeholder="">
        </section>
        <section>
          <span class="lable"></span>
          <input type="text" value="" placeholder="">
        </section>
        <section class="top">
          <span class="lable"></span>
          <img src="../../assets/uploadimg.png">
        </section>
      </div>
      <div class="btn_box">
        <span class="submit_btn"></span>
        <span class="cancel_btn"></span>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    data(){
      return{
        typesValue: '1',
        types:[
          {
            name: '',
            value: '1'
          },{
            name: '',
            value: '2'
          },
          {
            name: '',
            value: '3'
          },
          {
            name: '',
            value: '4'
          }
        ]
      }
    },
    methods:{
      types_tab:function(value){
        this.typesValue = value;
      }
    }
  }
</script>

<style media="screen">
 .applicationIn .tab_type_box{
   width: 100%;
     box-sizing: border-box
 }
 .applicationIn .tab_type_head a{
   width: 184px;
 }
 .applicationIn_content{
   padding-top: 30px;
   padding-left: 300px;
     font-size: 14px;
 }
 .applicationIn_content section{
   display: flex;
   height: 70px;
   justify-content: flex-start;
   align-items: center;
 }
 .applicationIn_content section.top{
   align-items: flex-start;
   padding: 10px 0;
   height: 100px;
 }
 .applicationIn_content section.top img{
   width: 100px;
   height: 100px;
 }
 .applicationIn_content section.top .lable{
   padding-top: 20px;
 }
 .applicationIn_content section .lable{
   width: 135px;
   color: #616161;
 }
 .applicationIn_content section .types{
   height: 40px;
     padding: 0 20px;
   line-height: 40px;
   text-align: center;
   margin-right: 10px;
   border: 1px solid #e5e7ed;
   background: #fff;
   color: #898989;
   cursor: pointer;
     transition: .3s;
 }
    .applicationIn_content section .types:hover{
        color: #006EFF
    }
 .applicationIn_content section .types.active{
   border: 1px solid #0066eb;
   background: #006EFF;
   color: #fff;
 }
 .applicationIn_content section input{
   width: 455px;
   height: 40px;
   padding-left: 10px;
   border: 1px solid #e5e7ed;
     font-size: 14px;
 }
 .applicationIn_content section select{
   width: 175px;
   height: 40px;
   border: 1px solid #e5e6ed;
   margin-right: 10px;
     font-size: 14px;
 }
 .btn_box{
   display: flex;
   justify-content: center;
   padding-bottom: 20px;
 }
 .btn_box span{
   width: 158px;
   height: 58px;
   margin: 10px;
 }
</style>



