<template>
  <div>
    <table class="common_table invoice_table">
      <tr>
        <th width="207" ></th>
        <th width="200"></th>
        <th width="330"></th>
        <th width="186" class="text_center"></th>
      </tr>
      <tr>
        <td class="text_left">3223432323424224</td>
        <td class="text_left"></td>
        <td class="text_left"></td>
        <td class="money">200.00</td>
      </tr>
    </table>
    <p class="title_invoice"></p>
    <ul class="manage_list_common manage_list_invoiceapply">
      <li>
        <p></p>
        <section>
          <span v-for="item in types" :key=item class="types" :class="{'active': typesValue=== item.value}" @click="types_tab(item.value)">{{item.name}}</span>
        </section>
      </li>
      <li>
        <p></p>
        <input type="text" placeholder="">
      </li>
      <li class="upload">
        <p></p>
        <section>
          <input type="text" placeholder="1520">
          <p class="des_1"></p>
        </section>
      </li>
      <li>
        <p></p>
        <input type="text" placeholder="">
      </li>
      <li>
        <p></p>
        <input type="text" placeholder="">
      </li>
      <li>
        <p></p>
        <input type="text" placeholder="">
      </li>
      <li>
        <p></p>
        <input type="text" placeholder="">
      </li>
      <li class="upload">
        <p></p>
        <img src="../../assets/uploadimg.png">
        <span></span>
      </li>
      <li class="upload">
        <p></p>
        <img src="../../assets/uploadimg.png">
      </li>
    </ul>
    <p class="title_invoice"></p>
    <p class="consignee_address">
      <span class="icon-map-marker"></span>
      <span></span>
      <span>120222010</span>
      <span>132****5220</span>
      <br />
      <span class="change_address"></span>
    </p>
    <div class="common_btn_box invoiceapply_btn_box">
      <span class="submit_btn invoiceapply_submit_btn"></span>
      <span class="cancel_btn invoiceapply_cancel_btn"></span>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    data(){
      return{
        typesValue: '1',
        types:[
          {
            name: '',
            value: '1'
          },{
            name: '',
            value: '2'
          },
          {
            name: '',
            value: '3'
          }
        ]
      }
    },
    methods:{
      types_tab:function(value){
        this.typesValue = value;
      }
    }
  }
</script>

<style media="screen">
  .invoice_table .money{
    color: #ff8a6e;
  }
  .manage_list_common.manage_list_invoiceapply li p:nth-child(1){
    padding-left: 35px;
    width: 175px; 
  }
  .manage_list_invoiceapply li section .types{
    border: 1px solid #e5e6ed;
    padding: 10px;
    color: #898989;
    margin-right: 10px;
    cursor: pointer;
  }
  .manage_list_invoiceapply li section .types.active{
    border: 1px solid #6389ed;
    background: #6389ed;
    color: #fff;
  }
  .title_invoice{
    padding-top: 40px;
    padding-bottom: 25px;
    border-bottom: 1px solid #e5e6ed;
    color: #616161;
    font-size: 14px;
    font-weight: 600;
  }
  .consignee_address{
    display: flex;
    height: 70px;
    justify-content: flex-start;
    align-items: center;
    padding-left: 20px;
    border-bottom: 1px solid #e5e6ed;
  }
  .consignee_address span{
    padding-right: 10px;
    color: #898989;
    font-size: 14px;
  }
  .consignee_address span:nth-child(1){
    font-size: 15px;
  }
  .consignee_address span:nth-child(2){
    font-weight: 600; 
  }
  .manage_list_invoiceapply li p.des_1{
    width: 600px;
    font-size: 12px;
    color: #898989;
    line-height: 30px;
  }
  .invoiceapply_btn_box{
    padding: 30px 0;
  }
  .invoiceapply_cancel_btn,.invoiceapply_submit_btn{
    width: 160px;
    height: 45px;
    line-height: 45px; 
    margin: 0 10px;
  }
  span.change_address{
    color: #6389ed;
    cursor: pointer;
  }
</style>



